(function() {
  this.App.service('MnoeDashboardTemplates', ["MnoeAdminApiSvc", function(MnoeAdminApiSvc) {
    var _getTemplates, _self;
    _self = this;
    this.list = function(limit, offset, sort) {
      var promise;
      return promise = MnoeAdminApiSvc.all("/impac/dashboard_templates").getList({
        order_by: sort,
        limit: limit,
        offset: offset
      }).then(function(response) {
        return response;
      });
    };
    this.templates = function(limit, offset, sort, params) {
      if (params == null) {
        params = {};
      }
      return _getTemplates(limit, offset, sort, params);
    };
    this["delete"] = function(dashboardTemplateId) {
      var promise;
      return promise = MnoeAdminApiSvc.one("/impac/dashboard_templates", dashboardTemplateId).remove().then(function(response) {
        return response;
      });
    };
    this.search = function(terms) {
      return MnoeAdminApiSvc.all("/impac/dashboard_templates").getList({
        terms: terms
      });
    };
    this.update = function(dashboardTemplate) {
      var promise;
      return promise = MnoeAdminApiSvc.one("/impac/dashboard_templates", dashboardTemplate.id).patch(dashboardTemplate).then(function(response) {
        return response;
      });
    };
    _getTemplates = function(limit, offset, sort, params) {
      if (params == null) {
        params = {};
      }
      params["order_by"] = sort;
      params["limit"] = limit;
      params["offset"] = offset;
      return MnoeAdminApiSvc.all("/impac/dashboard_templates").getList(params);
    };
    return this;
  }]);

}).call(this);
